.conocimientos{
    @include seccion($gris, $grisOscuro, $grisOscuro);
}

.conocimientos_contenido{

    @include tablet{
        @include grid(2,2rem);
    }

    h4{
        text-align: center;
    }

}

.conocimientos_lista{

    list-style: none;
    padding-left: 0;
    

    @include telefono{
        @include grid(2, 2rem);
    }

    li{
        border-bottom: 0.3rem solid $amarillo;
        margin-bottom: 2rem;
        font-weight: $bold;
        padding: 1rem;
        color: $blanco;
        background-color: $grisOscuro;
        border-radius: 5%;
        text-align: center;
        font-size: 2rem;
    }

}