/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}

/** Grid **/
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

/** Seccion **/
@mixin seccion($bgColor, $h2Color, $borderColor){

    background-color: $bgColor;
    padding-bottom: 3rem;

    h2{
        margin-top: 0;
        color: $h2Color;
        padding: 3rem;
        border-bottom: solid 0.2rem $borderColor;
    }

}