.header{

    background-color: $grisOscuro;

    @include tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem;
        }
    }

    h1{
        color: $blanco;
        text-align: center;
        @include tablet {
            margin: 0;
        }
    }

    .contenido-header{
        padding: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        @include tablet {
            flex-direction: row;
        }
    }

    span{
        color: $amarillo    ;
    }

}

.navegacion-principal{

    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    height: 0;

    @include tablet {
        display: flex;
        gap: 2rem;
        opacity: 1;
        visibility: visible;
        height: auto;
    }

    a{

        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: $blanco;
        font-size: 2rem;
        @include tablet {
            margin: 0;
        }
        
        &:hover{
            color: $amarillo;
        }
        
    }

}

/** Icono menu **/
.mobile-menu{

    @include tablet{
        display: none;
    }

    img{
        width: 5rem;
        margin-bottom: 2rem;
    }

}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}