/** Seccion **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  color: #323232;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
} /** UI **/
.header {
  background-color: #323232;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem;
  }
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header .contenido-header {
    flex-direction: row;
  }
}
.header span {
  color: #FFD371;
}

.navegacion-principal {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    opacity: 1;
    visibility: visible;
    height: auto;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #FFD371;
}

/** Icono menu **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  margin-bottom: 2rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.main {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main img {
  margin-top: 5rem;
  border-radius: 15%;
  height: 30rem;
  width: auto;
}
@media (min-width: 768px) {
  .main img {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .main {
    min-height: 65rem;
    justify-content: space-around;
    flex-direction: row;
  }
}

.main_contenido {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.main_contenido h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .main_contenido h1 {
    margin: 0;
  }
}
.main_contenido p {
  color: #FFFFFF;
  text-align: center;
}
.main_contenido span {
  color: #FFD371;
}
.main_contenido .boton {
  background-color: #FFD371;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .main_contenido .boton {
    margin-bottom: 0;
  }
}
.main_contenido .boton:hover {
  background-color: #ffcb58;
  cursor: pointer;
}

.webp .main {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/background.webp);
}

.no-webp .main {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/background.jpg);
}

.sobre-mi {
  background-color: #E1E1E1;
  padding-bottom: 3rem;
}
.sobre-mi h2 {
  margin-top: 0;
  color: #323232;
  padding: 3rem;
  border-bottom: solid 0.2rem #323232;
}

@media (min-width: 768px) {
  .sobre-mi_contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.sobre-mi_contenido h3 {
  color: #323232;
}

.competencias_contenido {
  list-style: none;
  padding-left: 0;
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .competencias_contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.competencias_contenido li {
  padding: 2rem;
  color: #FFFFFF;
  background-color: #323232;
  border-radius: 5%;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  border-bottom: 0.5rem solid #FFD371;
}
@media (min-width: 480px) {
  .competencias_contenido li {
    margin-bottom: 0;
  }
}

.formacion {
  background-color: #FFFFFF;
  padding-bottom: 3rem;
}
.formacion h2 {
  margin-top: 0;
  color: #323232;
  padding: 3rem;
  border-bottom: solid 0.2rem #323232;
}

@media (min-width: 768px) {
  .formacion_contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formacion_contenido h3 {
  color: #323232;
  text-align: center;
}
.formacion_contenido p {
  margin: 0;
  line-height: 1.4;
  font-weight: 700;
  text-align: center;
}
.formacion_contenido a {
  font-weight: 700;
  color: #323232;
}
.formacion_contenido a:hover {
  color: #FFFFFF;
}
.formacion_contenido h4 {
  margin: 0;
}

.formacion_apartado {
  margin: 3rem;
  padding: 2rem;
  background-color: #E1E1E1;
  border-radius: 5%;
}

.conocimientos {
  background-color: #E1E1E1;
  padding-bottom: 3rem;
}
.conocimientos h2 {
  margin-top: 0;
  color: #323232;
  padding: 3rem;
  border-bottom: solid 0.2rem #323232;
}

@media (min-width: 768px) {
  .conocimientos_contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.conocimientos_contenido h4 {
  text-align: center;
}

.conocimientos_lista {
  list-style: none;
  padding-left: 0;
}
@media (min-width: 480px) {
  .conocimientos_lista {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.conocimientos_lista li {
  border-bottom: 0.3rem solid #FFD371;
  margin-bottom: 2rem;
  font-weight: 700;
  padding: 1rem;
  color: #FFFFFF;
  background-color: #323232;
  border-radius: 5%;
  text-align: center;
  font-size: 2rem;
}

.contacto {
  background-color: #FFFFFF;
  padding-bottom: 3rem;
}
.contacto h2 {
  margin-top: 0;
  color: #323232;
  padding: 3rem;
  border-bottom: solid 0.2rem #323232;
}

@media (min-width: 768px) {
  .contacto_contenido {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contacto_contenido p {
  margin: 0;
  line-height: 1.4;
  font-weight: 700;
  text-align: center;
}
.contacto_contenido a {
  font-weight: 700;
  color: #323232;
}
.contacto_contenido a:hover {
  color: #FFFFFF;
}

.contacto_apartado {
  margin: 1.5rem;
  padding: 2rem;
  background-color: #E1E1E1;
  border-radius: 5%;
}

.footer {
  background: #323232;
  padding: 1.5rem 2.3rem;
  text-align: center;
}
.footer p {
  color: #FFFFFF;
}