.sobre-mi{
    @include seccion($gris, $grisOscuro, $grisOscuro);
}

.sobre-mi_contenido{
    
    @include tablet{
        @include grid(2,2rem);
    }

    h3{
        color: $grisOscuro;
    }

}

.competencias_contenido{

    list-style: none;
    padding-left: 0;
    margin-bottom: 1rem;

    @include telefono{
        @include grid(2, 2rem);
    }

    li{

        padding: 2rem;
        color: $blanco;
        background-color: $grisOscuro;
        border-radius: 5%;
        text-align: center;
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 2rem;
        border-bottom: 0.5rem solid $amarillo;

        @include telefono{
            margin-bottom: 0;
        }

    }

}