.formacion{
    @include seccion($blanco, $grisOscuro, $grisOscuro);
}

.formacion_contenido{
    
    @include tablet{
        @include grid(2,2rem);
    }

    h3{
        color: $grisOscuro;
        text-align: center;
    }

    p{
        margin: 0;
        line-height: 1.4;
        font-weight: $bold;
        text-align: center;
    }

    a{
        font-weight: $bold;
        color: $grisOscuro;
    }

    a:hover{
        color: $blanco;
    }

    h4{
        margin: 0;
    }

}

.formacion_apartado{
    margin: 3rem;
    padding: 2rem;
    background-color: $gris;
    border-radius: 5%;
}