.main{
    
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    display: flex;
    flex-direction: column;
    align-items: center;

    img{

        margin-top: 5rem;
        border-radius: 15%;
        height: 30rem;
        width: auto;

        @include tablet{
            margin-top: 0;
        }

    }
    
    @include tablet{
        min-height: 65rem;
        justify-content: space-around;
        flex-direction: row;
    }

}

.main_contenido{

    display:flex;
    align-items: center;
    justify-content:center;
    flex-direction: column;

    h1{
        color: $blanco;
        text-align: center;
        @include tablet {
            margin: 0;
        }
    }

    p{
        color: $blanco;
        text-align: center;
    }

    span{
        color: $amarillo;
    }

    .boton{

        background-color: $amarillo;
        color: $blanco;
        font-weight: $bold;
        text-decoration: none;
        padding: 1rem 3rem;
        text-align: center;
        margin-top: calc($separacion / 2);
        display: inline-block;
        border: none;
        margin-bottom: 5rem;

        @include tablet{
            margin-bottom: 0;
        }

        &:hover{
            background-color: darken($amarillo, 5);
            cursor: pointer;
        }

    }

}

.webp .main{
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/background.webp);
}

.no-webp .main{
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/background.jpg);
}