.contacto{
    @include seccion($blanco, $grisOscuro, $grisOscuro);
}

.contacto_contenido{

    @include tablet{
        @include grid(2,2rem);
    }

    p{
        margin: 0;
        line-height: 1.4;
        font-weight: $bold;
        text-align: center;
    }

    a{
        font-weight: $bold;
        color: $grisOscuro;
    }

    a:hover{
        color: $blanco;
    }

}

.contacto_apartado{
    margin: 1.5rem;
    padding: 2rem;
    background-color: $gris;
    border-radius: 5%;
}